import fs from 'node:fs/promises';
import path from 'node:path';
import { ENDPOINT, HEADERS, ALBUM_HASH } from '~/utils/imgur/constants';

type ImgurImage = {
  data: {
    id: string;
    deletehash: string;
    link: string;
  };
};

async function getPlaceholderImage() {
  const imagePath = path.resolve(
    __dirname,
    '../../../assets/images/placeholder.jpg'
  );
  const imageBuffer = await fs.readFile(imagePath);
  return imageBuffer.toString('base64');
}

export default async function uploadImage(image: string) {
  const formData = new URLSearchParams();
  formData.set('image', image);
  formData.set('album', ALBUM_HASH);

  try {
    const response = await fetch(ENDPOINT.IMAGE, {
      method: 'POST',
      headers: HEADERS,
      body: formData
    });
    const { data } = (await response.json()) as ImgurImage;
    console.log({ data });
    return { link: '' };
  } catch (error) {
    console.error(error);
  }
}
